.styletube{

    border: 40px solid #350455;
    
    width:650px;
}
.carroca{
    width:100%;

}
.carro-container{
    margin-top: 30px;
  
}

@media(min-width:1920px){

    .styletube{
        
        height: 500px;
        width:750px;
    }
    
}
@media(max-width:1100px){

    .styletube{
        
        height: 400px;
        width:550px;
    }
    
}


@media(max-width:900px){
    
    .styletube{
        
      
        width:400px;
    }
    
 }
 
 @media(max-width:700px){
    
    .styletube{
        
      
        width:400px;
    }
    
 }
 
 @media(max-width:500px){
    
    .styletube{
        
      
        width:280px;
    }
    
 }

 .rec-arrow[disabled] {
    transform: scale(0);
}