.YtContainer{


      height:250px;
      width: 450px;
      border: 8px solid #350455;


  

}

@media(max-width:1240px){
      .YtContainer{
       height:200px;
      width:400px;

      }
 }  
 

 @media(max-width:800px){
      .YtContainer{
       height:200px;
      width:320px;

      }
 }  


@media(min-width:320px) and (max-width:460px){
  

     .YtContainer{
      height:200px;
      width: 250px;

     }
}  


@media (max-width:350px){
  

     .YtContainer{
      height:200px;
      width:200px;

     }
}  




 @media(min-width:1920px){

      .YtContainer{
          height: 300px;
          width: 550px;
      }
  }
  

